.idle-check {
  display: block;
  margin-top: 3em;
}

.bar-chart {
  display: -ms-flexbox;
  display: flex;
  background-color: white;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  position: relative;
  padding: 20px;
}

.bar-chart dt {
  -ms-flex-item-align: start;
      align-self: flex-start;
  width: 100%;
  font-weight: 700;
  display: block;
  text-align: center;
  font-size: 1.2em;
  font-weight: 700;
  margin-bottom: 20px;
}

.bar-chart dd {
  margin: 0;
}

.bar-chart .text {
  font-weight: 600;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 40px;
  width: 6rem;
  background-color: white;
  position: absolute;
  left: 0;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.bar-chart .percentage {
  font-size: .8em;
  line-height: 1;
  text-transform: uppercase;
  width: calc(100% - 6rem);
  height: 40px;
  margin-left: 6rem;
  background: -webkit-repeating-linear-gradient(left, #ddd, #ddd 1px, #fff 1px, #fff 5%);
  background: -o-repeating-linear-gradient(left, #ddd, #ddd 1px, #fff 1px, #fff 5%);
  background: repeating-linear-gradient(to right, #ddd, #ddd 1px, #fff 1px, #fff 5%);
}

.bar-chart .percentage:after {
  content: "";
  display: block;
  background-color: #4e8941;
  width: 50px;
  margin-bottom: 10px;
  height: 90%;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  -webkit-transition: background-color .3s ease;
  -o-transition: background-color .3s ease;
  transition: background-color .3s ease;
  cursor: pointer;
  width: var(--value);
}

.bar-chart.percentage:hover:after, .percentage:focus:after {
  background-color: #aaa;
}



